<template>
	<view class="login">
		<view class="icon"></view>
		<view class="btn" @click="visit">抖音一键登录</view>
		<view class="promis">
			<checkbox-group style="font-size: 24rpx;color: #999999;" @change="check">
				<label v-for="item in list" :key="item.id">
					<checkbox :value="item.value" :checked="item.checked" style="transform:scale(0.7)" />{{item.name}}
				</label>
			</checkbox-group>
		</view>
	</view>
</template>

<script>
	//有素材
	//有会员，点击制作直接制作
	//没有会员,跳转会员中心
	//没有素材-----拉起弹窗
	//有会员，直接制作
	//没有会员,跳转会员中心
	import {
		login
	} from '../../api.js'
	import md5 from '../../utils/md5.js'
		import {decryptedFun} from 'utils/aes.js'
	export default {
		data() {
			return {
				list: [{
					value: "agree",
					name: "登录即同意《用户隐私政策》和《用户服务协议》",
					checked: true,
					id: 1
				}],
				value: [], //协议同意的判定条件
				openId: {}
			}
		},
		onLoad() {
			this.value.push('agree') //默认给value一个值,保持正常登录唤起状态,不给值,此时未进行操作时,value为[]
		},
		methods: {
			//登录的跳转
		  	visit() {
				console.log(this.openId)
				if (this.value.length == 0) {
					uni.showToast({
						title: "请同意协议后再登录"
					})
					return
				} else {
					let openId = uni.getStorageSync('openId')
					login({
						login_type: 'wx',
						openid: openId.openid,
						unionid: openId.unionid
					}).then(res => {
						let info = decryptedFun(res)
						uni.setStorageSync('info',info.result)
						if(info.msg == 'success'){
							// this.getInfo()
							uni.navigateBack()
						}else{
							uni.showToast({
								title:"请重新登录"
							})
						}
					})
					// uni.navigateTo({
					// 	url:"/pages/index/index"
					// })
				}
			},
			//协议判定选择框
			check(e) {
				// console.log(e.detail)
				let list = this.list
				list[0].checked = false
				let value = e.detail.value
				this.value = value
			}
		}
	}
</script>

<style lang="scss" scoped>
	$homeColor:#A9DDD8;

	.login {
		position: fixed;
		width: 100%;
		height: 100vh;
		background: $homeColor;

		.icon {
			width: 128rpx;
			height: 128rpx;
			background-image: url('data:image/png;base64,');
			background-repeat: no-repeat;
			background-size: 100% 100%;
			margin: 406rpx auto 274rpx;
		}

		.btn {
			width: 606rpx;
			height: 96rpx;
			background: #fff;
			border-radius: 48rpx;
			margin: 0 auto 40rpx;
			text-align: center;
			font-size: 28rpx;
			font-weight: 600;
			line-height: 96rpx;
		}

		.visitor {
			text-align: center;
			font-weight: normal;
			font-size: 24rpx;
			color: #213B52;
		}

		.promis {
			margin-top: 326rpx;
			text-align: center;
		}
	}
</style>
